<template>
  <div v-loading='loading'>
    <cms-breadcrumb to="/articleManager"  @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt collapse='true'>
        <li>带 * 为必填项</li>
      </cms-prompt>
      <el-form ref="form" :rules="updateRules" :model="formData" label-width="210px">
        <el-form-item label="文章分类" prop="cateId" :error='errorMessage.cateId'>
          <el-cascader :options="options" :props="props" :change-on-select='true' v-model="formData.parentIds">
          </el-cascader>
        </el-form-item>
        <el-form-item label="标题" prop="title"  :error='errorMessage.title' >
          <el-input v-model="formData.title" :autofocus="true" ref="first"></el-input>
        </el-form-item>
        <el-form-item label="排序值" prop="sortNum"  :error='errorMessage.sortNum' >
          <el-input v-model="formData.sortNum"></el-input>
        </el-form-item>
        <el-form-item label="是否显示" prop="show">
          <cms-switch v-model="formData.show"></cms-switch>
        </el-form-item>
         <el-form-item label="图片标识" prop="imgResourceId" >
          <cms-picture-zone @change="getLogo" v-model="formData.imgResourceId" :data="formData.imgResource">
          </cms-picture-zone>
        </el-form-item>
         <el-form-item label="作者" prop="author">
          <el-input v-model="formData.author"></el-input>
        </el-form-item>
         <el-form-item label="关键词(keywords)" prop="keywords">
          <el-input v-model="formData.keywords"></el-input>
        </el-form-item>
        <el-form-item label="描述(description)" prop="description">
          <el-input v-model="formData.description"></el-input>
        </el-form-item>
         <el-form-item label="外部链接" prop="linkUrl" :error='errorMessage.linkUrl'>
          <el-input v-model="formData.linkUrl"></el-input>
        </el-form-item>
        <el-form-item label="" prop="content" label-width="0">
          <cms-editor v-model="formData.content"></cms-editor>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
         <el-button @click="update" type="primary" size="small">保存修改</el-button>
        <el-button  @click="reset"  size="small">重置</el-button>
     
      </div>
    </div>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import { artManagerEdit, artManagerGet } from '@/api/article/articleManager.js'
import {
  artCategoryTree
} from '@/api/article/articleCategory.js'
var defaultData = {
  'title': '',
  'show': true,
  'author': '',
  'keywords': '',
  'description': '',
  'linkUrl': '',
  'content': '',
  'cateId': '', // 根目录值
  'parentIds': [0],
  'imgResourceId': '',
  'imgResource': '',
  'sortNum': 10,
  'id': ''
}
export default {
  name: 'articleManagerEdit',
  mixins: [form],
  data() {
    var validateSortnum = (rule, value, callback) => {
      if (value >= 1 && value <= 100) {
        callback()
      } else {
        callback(new Error('排序值为1到100的整数'))
      }
    }
    return {
      options: [],
      props: {
        value: 'id',
        label: 'name',
        children: 'childs',
        disabled: 'isAuth'
      },
      formData: Object.assign({}, defaultData),
      updateRules: {// 修改规则
        title: [rules.required('请输入标题'), rules.max(20)],
        cateId: [rules.required('必须选择一个上级分类')],
        sortNum: [rules.required('请输入排序值'), rules.number(), rules.custom(validateSortnum)],
        author: [rules.max(20)],
        keywords: [rules.max(20)],
        description: [rules.max(20)],
        linkUrl: [rules.url()]
      }

    }
  },
  methods: {
    getFormData(id) { // 获取分类信息
      artManagerGet({ id: id }).then(res => {
        this.formData = res.data
        this.formData.parentIds = [res.data.cateId]
      })
    },
    getLogo(items) { // 上传图片方法
      this.formData.iconId = items[0].id
    },
    getMenuTree() { // 获取菜单节点
      artCategoryTree().then(
        res => {
          this.options = res.data
        }
      )
    },
    update() {
      const params = this.$cms.deepCopy(this.formData)
      params.cateId = params.parentIds[params.parentIds.length - 1]

      this.updateForm(artManagerEdit, params, '/articleManager/list', 'list')
    },
    reset() {
      this.getFormData(this.$route.query.id)
      this.$refs.form.resetFields()
    },
    getAll() {
      this.getFormData(this.$route.query.id)
      this.getMenuTree()
    }
  },
  created() {
    this.getAll()
  }
}
</script>




